import React from 'react';
import { Droppable, Draggable } from 'react-beautiful-dnd';
import { PlusIcon, XMarkIcon, CheckIcon, ChevronDownIcon } from '@heroicons/react/24/outline';
import { v4 as uuidv4 } from 'uuid';

const ContentSection = ({ sections, onUpdateSection }) => {
  
  const addItem = (sectionName, newItem) => {
    const currentItems = sections[sectionName] || [];
    onUpdateSection(sectionName, [...currentItems, { ...newItem, id: uuidv4() }]);
  };

  const removeItem = (sectionName, itemId) => {
    const currentItems = sections[sectionName] || [];
    onUpdateSection(sectionName, currentItems.filter(item => item.id !== itemId));
  };

  const updateItem = (sectionName, itemId, updates) => {
    const currentItems = sections[sectionName] || [];
    onUpdateSection(sectionName, currentItems.map(item => 
      item.id === itemId ? { ...item, ...updates } : item
    ));
  };

  const toggleFAQ = (itemId) => {
    updateItem('faq', itemId, { isOpen: !sections.faq.find(item => item.id === itemId)?.isOpen });
  };

  return (
    <div className="space-y-8 mt-8">
      {/* What's Included Section */}
      <div className="pt-6 border-t border-amazon-border-light">
        <h2 className="text-xl font-bold mb-5 text-amazon-text">What's included</h2>
        <Droppable droppableId="included" type="included">
          {(provided) => (
            <div {...provided.droppableProps} ref={provided.innerRef} className="space-y-0 mb-6">
              {sections.included?.map((item, index) => (
                <Draggable key={item.id} draggableId={item.id} index={index}>
                  {(provided, snapshot) => (
                    <div
                      ref={provided.innerRef}
                      {...provided.draggableProps}
                      className={`group flex items-start py-3 border-b border-gray-100 hover:bg-amazon-bg rounded px-2 -mx-2 ${
                        snapshot.isDragging ? 'shadow-lg bg-white' : ''
                      }`}
                    >
                      <div {...provided.dragHandleProps} className="mr-2 mt-1 cursor-grab">
                        <div className="w-2 h-4 flex flex-col justify-center space-y-0.5">
                          <div className="w-full h-0.5 bg-gray-400 rounded"></div>
                          <div className="w-full h-0.5 bg-gray-400 rounded"></div>
                          <div className="w-full h-0.5 bg-gray-400 rounded"></div>
                        </div>
                      </div>
                      <div className="flex-1">
                        <input
                          type="text"
                          value={item.title}
                          onChange={(e) => updateItem('included', item.id, { title: e.target.value })}
                          className="font-semibold text-sm text-amazon-text mb-1 w-full bg-transparent border-none outline-none"
                          placeholder="Item title"
                        />
                        <textarea
                          value={item.description}
                          onChange={(e) => updateItem('included', item.id, { description: e.target.value })}
                          className="text-sm text-amazon-text-light leading-5 w-full bg-transparent border-none outline-none resize-none"
                          placeholder="Item description"
                          rows={2}
                        />
                      </div>
                      <button
                        onClick={() => removeItem('included', item.id)}
                        className="ml-4 p-1 text-red-500 hover:text-red-700 opacity-0 group-hover:opacity-100 transition-opacity"
                      >
                        <XMarkIcon className="w-4 h-4" />
                      </button>
                    </div>
                  )}
                </Draggable>
              ))}
              {provided.placeholder}
            </div>
          )}
        </Droppable>
        <button
          onClick={() => addItem('included', { title: 'New Item', description: 'Description of the new item' })}
          className="flex items-center gap-2 text-amazon-orange hover:text-amazon-orange-dark font-medium text-sm"
        >
          <PlusIcon className="w-4 h-4" />
          Add Item
        </button>
      </div>

      {/* Program Details Section */}
      <div className="pt-6 border-t border-amazon-border-light">
        <h2 className="text-xl font-bold mb-5 text-amazon-text">Program Details</h2>
        <Droppable droppableId="programDetails" type="programDetails">
          {(provided) => (
            <div {...provided.droppableProps} ref={provided.innerRef} className="space-y-4 mb-6">
              {sections.programDetails?.map((item, index) => (
                <Draggable key={item.id} draggableId={item.id} index={index}>
                  {(provided, snapshot) => (
                    <div
                      ref={provided.innerRef}
                      {...provided.draggableProps}
                      className={`group flex items-start py-3 hover:bg-amazon-bg rounded px-2 -mx-2 ${
                        snapshot.isDragging ? 'shadow-lg bg-white' : ''
                      }`}
                    >
                      <div {...provided.dragHandleProps} className="mr-3 mt-0.5 cursor-grab">
                        <div className="w-2 h-4 flex flex-col justify-center space-y-0.5">
                          <div className="w-full h-0.5 bg-gray-400 rounded"></div>
                          <div className="w-full h-0.5 bg-gray-400 rounded"></div>
                          <div className="w-full h-0.5 bg-gray-400 rounded"></div>
                        </div>
                      </div>
                      <div className="w-6 h-6 rounded-full bg-amazon-orange flex items-center justify-center mr-3 mt-0.5 flex-shrink-0">
                        <CheckIcon className="w-3 h-3 text-white" />
                      </div>
                      <textarea
                        value={item.text}
                        onChange={(e) => updateItem('programDetails', item.id, { text: e.target.value })}
                        className="flex-1 text-sm text-amazon-text leading-5 bg-transparent border-none outline-none resize-none"
                        placeholder="Program detail description"
                        rows={2}
                      />
                      <button
                        onClick={() => removeItem('programDetails', item.id)}
                        className="ml-4 p-1 text-red-500 hover:text-red-700 opacity-0 group-hover:opacity-100 transition-opacity"
                      >
                        <XMarkIcon className="w-4 h-4" />
                      </button>
                    </div>
                  )}
                </Draggable>
              ))}
              {provided.placeholder}
            </div>
          )}
        </Droppable>
        <button
          onClick={() => addItem('programDetails', { text: 'New program detail description' })}
          className="flex items-center gap-2 text-amazon-orange hover:text-amazon-orange-dark font-medium text-sm"
        >
          <PlusIcon className="w-4 h-4" />
          Add Detail
        </button>
      </div>

      {/* Customer Testimonials Section */}
      <div className="pt-6 border-t border-amazon-border-light">
        <h2 className="text-xl font-bold mb-5 text-amazon-text">What customers are saying</h2>
        <Droppable droppableId="testimonials" type="testimonials">
          {(provided) => (
            <div {...provided.droppableProps} ref={provided.innerRef} className="grid grid-cols-1 md:grid-cols-2 gap-6 mb-6">
              {sections.testimonials?.map((item, index) => (
                <Draggable key={item.id} draggableId={item.id} index={index}>
                  {(provided, snapshot) => (
                    <div
                      ref={provided.innerRef}
                      {...provided.draggableProps}
                      className={`group bg-white border border-amazon-border-light rounded-lg p-6 hover:shadow-md transition-shadow relative ${
                        snapshot.isDragging ? 'shadow-lg' : ''
                      }`}
                    >
                      <div {...provided.dragHandleProps} className="absolute top-2 left-2 cursor-grab">
                        <div className="w-2 h-4 flex flex-col justify-center space-y-0.5">
                          <div className="w-full h-0.5 bg-gray-400 rounded"></div>
                          <div className="w-full h-0.5 bg-gray-400 rounded"></div>
                          <div className="w-full h-0.5 bg-gray-400 rounded"></div>
                        </div>
                      </div>
                      <div className="flex items-center mb-3">
                        <div className="flex text-amazon-orange text-sm">
                          {''.repeat(item.rating)}
                        </div>
                        <span className="ml-2 text-sm text-amazon-text-light">Verified Purchase</span>
                      </div>
                      <textarea
                        value={item.text}
                        onChange={(e) => updateItem('testimonials', item.id, { text: e.target.value })}
                        className="text-sm text-amazon-text leading-5 mb-4 w-full bg-transparent border-none outline-none resize-none"
                        placeholder="Customer testimonial..."
                        rows={3}
                      />
                      <input
                        type="text"
                        value={item.author}
                        onChange={(e) => updateItem('testimonials', item.id, { author: e.target.value })}
                        className="text-xs text-amazon-text-light w-full bg-transparent border-none outline-none"
                        placeholder=" Author Name, verified customer"
                      />
                      <button
                        onClick={() => removeItem('testimonials', item.id)}
                        className="absolute top-2 right-2 p-1 text-red-500 hover:text-red-700 opacity-0 group-hover:opacity-100 transition-opacity"
                      >
                        <XMarkIcon className="w-4 h-4" />
                      </button>
                    </div>
                  )}
                </Draggable>
              ))}
              {provided.placeholder}
            </div>
          )}
        </Droppable>
        <button
          onClick={() => addItem('testimonials', { 
            rating: 5, 
            text: 'New testimonial content goes here...', 
            author: ' Author Name, verified customer' 
          })}
          className="flex items-center gap-2 text-amazon-orange hover:text-amazon-orange-dark font-medium text-sm"
        >
          <PlusIcon className="w-4 h-4" />
          Add Testimonial
        </button>
      </div>

      {/* FAQ Section */}
      <div className="pt-6 border-t border-amazon-border-light">
        <h2 className="text-xl font-bold mb-5 text-amazon-text">Frequently Asked Questions</h2>
        <Droppable droppableId="faq" type="faq">
          {(provided) => (
            <div {...provided.droppableProps} ref={provided.innerRef} className="space-y-4 mb-6">
              {sections.faq?.map((item, index) => (
                <Draggable key={item.id} draggableId={item.id} index={index}>
                  {(provided, snapshot) => (
                    <div
                      ref={provided.innerRef}
                      {...provided.draggableProps}
                      className={`group border border-amazon-border-light rounded-lg overflow-hidden relative ${
                        snapshot.isDragging ? 'shadow-lg' : ''
                      }`}
                    >
                      <div {...provided.dragHandleProps} className="absolute top-2 left-2 cursor-grab z-10">
                        <div className="w-2 h-4 flex flex-col justify-center space-y-0.5">
                          <div className="w-full h-0.5 bg-gray-400 rounded"></div>
                          <div className="w-full h-0.5 bg-gray-400 rounded"></div>
                          <div className="w-full h-0.5 bg-gray-400 rounded"></div>
                        </div>
                      </div>
                      <div 
                        className="bg-amazon-bg p-4 cursor-pointer hover:bg-gray-100 pl-8"
                        onClick={() => toggleFAQ(item.id)}
                      >
                        <div className="flex items-center justify-between">
                          <input
                            type="text"
                            value={item.question}
                            onChange={(e) => updateItem('faq', item.id, { question: e.target.value })}
                            className="font-medium text-sm text-amazon-text bg-transparent border-none outline-none flex-1 mr-4"
                            placeholder="New question?"
                            onClick={(e) => e.stopPropagation()}
                          />
                          <ChevronDownIcon className={`w-4 h-4 text-amazon-text-light transition-transform ${
                            item.isOpen ? 'rotate-180' : ''
                          }`} />
                        </div>
                      </div>
                      {item.isOpen && (
                        <div className="p-4 bg-white">
                          <textarea
                            value={item.answer}
                            onChange={(e) => updateItem('faq', item.id, { answer: e.target.value })}
                            className="text-sm text-amazon-text leading-5 w-full bg-transparent border-none outline-none resize-none"
                            placeholder="Answer to the question..."
                            rows={3}
                          />
                        </div>
                      )}
                      <button
                        onClick={() => removeItem('faq', item.id)}
                        className="absolute top-2 right-2 p-1 text-red-500 hover:text-red-700 opacity-0 group-hover:opacity-100 transition-opacity z-10"
                      >
                        <XMarkIcon className="w-4 h-4" />
                      </button>
                    </div>
                  )}
                </Draggable>
              ))}
              {provided.placeholder}
            </div>
          )}
        </Droppable>
        <button
          onClick={() => addItem('faq', { 
            question: 'New question?', 
            answer: 'Answer to the new question...', 
            isOpen: false 
          })}
          className="flex items-center gap-2 text-amazon-orange hover:text-amazon-orange-dark font-medium text-sm"
        >
          <PlusIcon className="w-4 h-4" />
          Add FAQ
        </button>
      </div>
    </div>
  );
};

export default ContentSection;






