import React, { useState, useEffect } from 'react';
import { DragDropContext } from 'react-beautiful-dnd';
import AmazonHeader from './components/AmazonHeader';
import ProductSection from './components/ProductSection';
import ContentSection from './components/ContentSection';
import ControlPanel from './components/ControlPanel';
import { useLocalStorage } from './hooks/useLocalStorage';
import { exportToPDF, exportToHTML } from './utils/exportUtils';
import './styles/compiled.css';

function App() {
  // Main application state
  const [projectData, setProjectData] = useLocalStorage('amazon-onboarding-data', {
    title: 'Fertility and Family Building | Treatment coverage and personalized support to fulfill your dreams of family',
    brand: 'Progyny',
    price: '$0',
    priceUnit: '99',
    description: 'Progyny can help to make your family dreams come true through a fully supported journey. We connect you with the nation\'s top fertility specialists to receive the quality care you deserve and partner you with a Progyny Care Advocate, our fertility experts trained to support all paths to parenthood including adoption and surrogacy, for unlimited emotional support and care navigation.',
    ctaText: 'Check Coverage',
    breadcrumb: 'Health & Health',
    images: {
      hero: null,
      thumbnails: []
    },
    sections: {
      included: [
        {
          id: '1',
          title: 'Support for all paths to parenthood',
          description: 'Fertility and family building coverage (IVF, IUI, egg freezing, and more) from top fertility specialists, support for adoption and surrogacy, and preconception coaching & trying to conceive resources'
        },
        {
          id: '2',
          title: 'Personalized support',
          description: '1:1 Unlimited phone and messaging support from a dedicated Progyny Care Advocate to help coordinate care and provide emotional support'
        }
      ],
      programDetails: [
        {
          id: '1',
          text: 'Coverage for fertility treatments including IVF, IUI, egg freezing, genetic testing, and fertility medications'
        },
        {
          id: '2',
          text: 'Access to Progyny\'s network of top reproductive endocrinologists and fertility specialists'
        },
        {
          id: '3',
          text: 'Dedicated Progyny Care Advocate for personalized support and care coordination'
        }
      ],
      testimonials: [
        {
          id: '1',
          rating: 5,
          text: 'Progyny made our fertility journey so much easier. The Care Advocate was incredibly supportive and helped us navigate every step of the process.',
          author: ' Sarah M., verified customer'
        },
        {
          id: '2',
          rating: 5,
          text: 'The coverage was comprehensive and the specialists in their network were top-notch. We\'re so grateful for the support we received.',
          author: ' Michael & Jennifer K., verified customers'
        }
      ],
      faq: [
        {
          id: '1',
          question: 'How do I check if my employer offers Progyny benefits?',
          answer: 'You can check your employee benefits portal or contact your HR department to see if Progyny is included in your health benefits package. You can also visit the Progyny website and use their employer lookup tool.',
          isOpen: false
        },
        {
          id: '2',
          question: 'What fertility treatments are covered?',
          answer: 'Progyny covers a wide range of fertility treatments including IVF, IUI, egg freezing, genetic testing, fertility medications, and more. Coverage may vary by employer plan, so check with your Care Advocate for specific details.',
          isOpen: false
        },
        {
          id: '3',
          question: 'How do I get started with Progyny?',
          answer: 'Simply click "Check Coverage" above to get started. You\'ll be connected with a Progyny Care Advocate who will help you understand your benefits and guide you through the next steps in your fertility journey.',
          isOpen: false
        }
      ]
    }
  });

  const [isLoading, setIsLoading] = useState(false);
  const [notification, setNotification] = useState(null);

  // Auto-save functionality
  useEffect(() => {
    const autoSaveTimer = setTimeout(() => {
      // Auto-save is handled by useLocalStorage hook
      console.log('Auto-saved project data');
    }, 2000);

    return () => clearTimeout(autoSaveTimer);
  }, [projectData]);

  // Update project data
  const updateProjectData = (updates) => {
    setProjectData(prev => ({
      ...prev,
      ...updates
    }));
  };

  // Update specific section
  const updateSection = (sectionName, data) => {
    setProjectData(prev => ({
      ...prev,
      sections: {
        ...prev.sections,
        [sectionName]: data
      }
    }));
  };

  // Handle drag and drop reordering
  const handleDragEnd = (result) => {
    if (!result.destination) return;

    const { source, destination, type } = result;
    
    if (source.droppableId === destination.droppableId && source.index === destination.index) {
      return;
    }

    const sectionData = [...projectData.sections[type]];
    const [removed] = sectionData.splice(source.index, 1);
    sectionData.splice(destination.index, 0, removed);

    updateSection(type, sectionData);
  };

  // Export functions
  const handleExportPDF = async () => {
    setIsLoading(true);
    try {
      await exportToPDF(projectData);
      showNotification('PDF exported successfully!', 'success');
    } catch (error) {
      showNotification('Failed to export PDF: ' + error.message, 'error');
    } finally {
      setIsLoading(false);
    }
  };

  const handleExportHTML = async () => {
    setIsLoading(true);
    try {
      await exportToHTML(projectData);
      showNotification('HTML exported successfully!', 'success');
    } catch (error) {
      showNotification('Failed to export HTML: ' + error.message, 'error');
    } finally {
      setIsLoading(false);
    }
  };

  // Save/Load project
  const handleSaveProject = async () => {
    try {
      const dataStr = JSON.stringify(projectData, null, 2);
      const dataBlob = new Blob([dataStr], { type: 'application/json' });
      
      if (window.showSaveFilePicker) {
        const fileHandle = await window.showSaveFilePicker({
          suggestedName: 'amazon-onboarding-project.json',
          types: [{
            description: 'Amazon Onboarding Projects',
            accept: { 'application/json': ['.json'] }
          }]
        });
        
        const writable = await fileHandle.createWritable();
        await writable.write(dataBlob);
        await writable.close();
      } else {
        // Fallback for browsers without File System Access API
        const url = URL.createObjectURL(dataBlob);
        const a = document.createElement('a');
        a.href = url;
        a.download = 'amazon-onboarding-project.json';
        a.click();
        URL.revokeObjectURL(url);
      }
      
      showNotification('Project saved successfully!', 'success');
    } catch (error) {
      showNotification('Failed to save project: ' + error.message, 'error');
    }
  };

  const handleLoadProject = async () => {
    try {
      if (window.showOpenFilePicker) {
        const [fileHandle] = await window.showOpenFilePicker({
          types: [{
            description: 'Amazon Onboarding Projects',
            accept: { 'application/json': ['.json'] }
          }]
        });
        
        const file = await fileHandle.getFile();
        const contents = await file.text();
        const data = JSON.parse(contents);
        setProjectData(data);
      } else {
        // Fallback for browsers without File System Access API
        const input = document.createElement('input');
        input.type = 'file';
        input.accept = '.json';
        input.onchange = async (e) => {
          const file = e.target.files[0];
          if (file) {
            const contents = await file.text();
            const data = JSON.parse(contents);
            setProjectData(data);
          }
        };
        input.click();
      }
      
      showNotification('Project loaded successfully!', 'success');
    } catch (error) {
      showNotification('Failed to load project: ' + error.message, 'error');
    }
  };

  // Notification system
  const showNotification = (message, type = 'info') => {
    setNotification({ message, type });
    setTimeout(() => setNotification(null), 4000);
  };

  return (
    <div className="min-h-screen bg-white">
      <DragDropContext onDragEnd={handleDragEnd}>
        {/* Amazon Header */}
        <AmazonHeader />
        
        {/* Main Content */}
        <main className="max-w-amazon mx-auto px-4 bg-white font-amazon">
          {/* Breadcrumb */}
          <nav className="pt-4 pb-2 text-xs text-amazon-text-light">
            <input
              type="text"
              value={projectData.breadcrumb}
              onChange={(e) => updateProjectData({ breadcrumb: e.target.value })}
              className="bg-transparent border-none outline-none cursor-pointer hover:text-amazon-red hover:underline"
            />
          </nav>

          {/* Product Section */}
          <ProductSection
            data={projectData}
            onUpdate={updateProjectData}
            onImageUpload={(imageData, type) => {
              if (type === 'hero') {
                updateProjectData({
                  images: { ...projectData.images, hero: imageData }
                });
              } else {
                const newThumbnails = [...projectData.images.thumbnails, imageData];
                updateProjectData({
                  images: { ...projectData.images, thumbnails: newThumbnails }
                });
              }
            }}
          />

          {/* Content Sections */}
          <ContentSection
            sections={projectData.sections}
            onUpdateSection={updateSection}
          />
        </main>

        {/* Control Panel */}
        <ControlPanel
          onSave={handleSaveProject}
          onLoad={handleLoadProject}
          onExportPDF={handleExportPDF}
          onExportHTML={handleExportHTML}
          isLoading={isLoading}
        />

        {/* Notification */}
        {notification && (
          <div className={`fixed top-4 right-4 p-4 rounded-lg shadow-lg z-50 ${
            notification.type === 'error' ? 'bg-red-500' : 
            notification.type === 'success' ? 'bg-green-500' : 'bg-blue-500'
          } text-white`}>
            {notification.message}
          </div>
        )}
      </DragDropContext>
    </div>
  );
}

export default App;





